  blockunblockuser() {
    if (this.blockType == 'room') {
      this.api.un_block({
        roomId: this.blockId
      }).subscribe((result) => { this.getRoom() })
    }
    if (this.blockType == 'userSpent') {
      this.api.un_blockUser({
        userId: this.blockId
      }).subscribe((result) => { this.getSpent() })
    }
    if (this.blockType == 'userReceive') {
      this.api.un_blockUser({
        userId: this.blockId
      }).subscribe((result) => { this.getReceived() })
    }
  }

