import { Component, OnInit, ViewChild } from '@angular/core';
import { MatPaginator, MatSort, MatTableDataSource } from '@angular/material';
import { DatepickerOptions } from 'ng2-datepicker';
import { AuthenticationService } from "../../service/authentication.service";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
declare var $: any;

export interface PeriodicElement{
  id:string;
  name:string;
  image:string;
  
}
const ELEMENT_DATA:PeriodicElement[]= [
  {
    id:"345",
    name:"George",
    image:"",
  },
];


@Component({
  selector: 'app-package-module-management',
  templateUrl: './package-module-management.component.html',
  styleUrls: ['./package-module-management.component.css']
})
export class PackageModuleManagementComponent implements OnInit {

  click : boolean = false;
  displayedColumns: string[] = [
    "checkbox",
    "id",
    "name",
    "image",    
    "action",
  ];
  dataSource = new MatTableDataSource<PeriodicElement>(ELEMENT_DATA);
  public doFilter = (value: string) => {
    this.dataSource.filter = value.trim().toLocaleLowerCase();
  };

  @ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
  @ViewChild(MatSort, { static: true }) sort: MatSort;
  

  constructor(private api: AuthenticationService,
    private formBuilder: FormBuilder ) { }
  pModule
  packageForm
  editpackageForm
  ngOnInit() {
    this.getPackageModules()
    this.packageForm = this.formBuilder.group({
      title: ['', Validators.required], 
    });
    this.editpackageForm = this.formBuilder.group({
      title: ['', Validators.required], 
    });
  }

  getPackageModules() {
    this.api.getPackageModule().subscribe(
      (res) => {
        this.pModule = res["response"].reverse()
        console.log(this.pModule)
        this.dataSource = new MatTableDataSource(this.pModule);
        this.dataSource.paginator = this.paginator;
        this.dataSource.sort = this.sort;
      },
      (err) => {
        console.log(err);
      }
    );
  }

  sortfilter(event){
    let sort = event.target.value;
    if(sort === "1"){
      console.log(this.pModule)
       this.pModule.sort((a,b) => (a.title > b.title) ? 1 : ((b.title > a.title) ? -1 : 0));
       this.dataSource = new MatTableDataSource<PeriodicElement>(this.pModule);
       this.dataSource.paginator = this.paginator;
    } 
    if(sort === "2"){
     this.pModule.sort((a,b) => (a.title < b.title) ? 1 : ((b.title < a.title) ? -1 : 0));
     this.dataSource = new MatTableDataSource<PeriodicElement>(this.pModule);
     this.dataSource.paginator = this.paginator;
    }
  }
  selImage
  single:any[];
  picture:any;
  singleFilesForPackage(event) {
    this.selImage =true
      const file1 = event.target.files[0];
      this.single = [];
      var singleFiles = event.target.files;
      if (singleFiles) {
        for (var file of singleFiles) {
          var singleReader = new FileReader();
          singleReader.onload = (e: any) => {
            this.single.push(e.target.result);
            document.getElementById("imagefile1").setAttribute("src", e.target.result);
            document.getElementById("imagefile2").setAttribute("src", e.target.result);  
          };
          singleReader.readAsDataURL(file);
        }
      }
      this.picture = event.target.files[0];
    }

    get f(){
      return this.packageForm.controls;
    }
    get g(){
      return this.editpackageForm.controls;
    }
    

    submitted
    saveMemberShip(){
      if(this.packageForm.invalid) return;
      if(this.picture == '' || this.picture == null ) {
        this.api.toaster("error","Pease select image",900)
        return
      }
      this.submitted = true;
      console.log(this.packageForm.invalid,"tur",this.packageForm.controls)
      let adddata = new FormData();
      adddata.append('title',this.packageForm.value.title)
      adddata.append('image',this.picture)
      console.log(adddata)
      this.api.addPackageModule(adddata).subscribe(
        (adddata)=>{
          this.api.toaster("success","Package Mopdule Save Successfully",900) //toaster 
          this.getPackageModules();
          $('#add').modal('hide');
          this.packageForm.reset();
        },
        error=> {
          console.log(error);
           this.api.toaster("error",error.error.message)
        })
    }

    membershipId
    blockid(id) {
      this.membershipId = id;
    }

    deletePackageModule() {
      var datadelete = {
        packageId: this.membershipId,
      };
      this.api.deletePackageModule(datadelete).subscribe(
        (d) => {
          if (d) {
            this.getPackageModules();
          }
          setTimeout(() => {
            $("#deleted-success").modal("hide");
          }, 1200);
        },
        (error) => {
          console.log(error);
        }
      );
    }
    mData
    mImages
    mId
    editMem(dataM) {
      this.mData = dataM
      this.mId = dataM._id
        this.editpackageForm.patchValue({ 
          title : this.mData.title,
        });
        this.mImages = this.mData.image
        document.getElementById("imagefile2").setAttribute('src',this.mData.image);
    }

    updatePackageModule(){
      if(this.editpackageForm.invalid) return;
      console.log(this.editpackageForm.invalid)
      let editdata = new FormData();
      editdata.append('packageId', this.mId)
      editdata.append('title',this.editpackageForm.value.title)
      editdata.append('image', this.selImage == true ? this.picture  : this.mImages)
      this.api.editPackageModule(editdata).subscribe(
        (abc)=>{
          this.api.toaster("success","Package module Updated Successfully",900) //toaster 
          this.getPackageModules();
          $('#packageedit').modal('hide');
        this.editpackageForm.reset();
        this.picture = ""
        document.getElementById("imagefile2").setAttribute('src',"assets/img/user.jpg");
        },
        error=> {
          console.log(error);
           this.api.toaster("error",error.error.message)
        })
  
    }
}
