import { Component, OnInit, ViewChild } from '@angular/core';
import { MatPaginator, MatSort, MatTableDataSource } from "@angular/material";
import { DatepickerOptions } from "ng2-datepicker";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { AuthenticationService } from "../../service/authentication.service";
import { Router, ActivatedRoute } from "@angular/router";
import * as enLocale from "date-fns/locale/en";
import { ToastrService } from 'ngx-toastr';
declare var $: any;
export interface PeriodicElement {
  id: string;
  name: string;
  joiningdate: string;
  email:string;
  mobile: number;
  country: string; 
}

const ELEMENT_DATA: PeriodicElement[] = [
  {
    id: "232",
    name: "amit",
    joiningdate: "data",
    email:"amit@gmail.com",
    mobile: 9988776655,
    country: " India",   
  },
];

@Component({
  selector: 'app-send-coin',
  templateUrl: './send-coin.component.html',
  styleUrls: ['./send-coin.component.css']
})
export class SendCoinComponent implements OnInit {
  displayedColumns: string[] = [
    "checkbox",
    "id",
    "name",
    "joiningdate",
    "email",
    "mobile",
    "country",    
    "action",
  ];
  dataSource = new MatTableDataSource<PeriodicElement>(ELEMENT_DATA);
  public doFilter = (value: string) => {
    this.dataSource.filter = value.trim().toLocaleLowerCase();
  };

  @ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
  @ViewChild(MatSort, { static: true }) sort: MatSort;
  
  todate: Date;
  // todate: any;
  optionsFrom: DatepickerOptions = {
    minYear: 2017,
    maxDate: new Date(),
    locale: enLocale,
  };
  userList: any;
  inputValue: any;
  nocoin: boolean;

  sendCoinForm:FormGroup;
  data: {};
  optionsTo: { minDate: Date; maxDate: Date; };
  endDate: any;
  allCoin: any;
  forFilter: any;
  fromdate: number;
  errorMessage: any;

  constructor(
    private formBuilder: FormBuilder,
    private api: AuthenticationService,
    private router: Router,
    private route: ActivatedRoute,
    private toast : ToastrService
  ){}

  ngOnInit() {
    this.getAllUserList();
    this.sendCoinForm = this.formBuilder.group({
      coin: ['', Validators.required],
    });
  
  }

  

  getAllUserList() {
    this.api.getuserlist().subscribe(
      (res) => {
        console.log(res,":::::::::::::::::::after api");
        // this.dataSource = new MatTableDataSource(res['response']);
        // console.log(this.dataSource,"lll")
        this.userList = res["userList"];
        this.dataSource = new MatTableDataSource(res['userList']);
        this.dataSource.paginator = this.paginator;
        this.dataSource.sort = this.sort;
      },
      (err) => {
        console.log(err);
      }
    );
  }

  // pickupID(id){
  //   this.country_id = id
  //   console.log(this.country_id,"pickup")
  // }

  sendCoin(id) {
    if(this.sendCoinForm.value.coin === "" || this.sendCoinForm.value.coin === null){
      this.api.toaster("error","Coin value required.")
      return 
    }
    console.log(id)
    this.inputValue = parseInt(this.sendCoinForm.value.coin),
    
     this.data = {
      userID:id,
      coinQuantity:this.inputValue,
    }
    console.log(this.inputValue,"input::::::::::::::::::::");
    this.api.saveCoin(this.data).subscribe((res:any)=>{
      this.api.toaster("success","Coin send Successfully.")
      console.log(res,"response");
      this.sendCoinForm.reset();
    },(error) => {
      console.log(error);
        this.errorMessage = error.error.message
        this.api.toaster("error",this.errorMessage,900);
    })
  }

  sortfilter(event){
    let sort = event.target.value;
    // console.log(sort,"boom sort",this.subAdminList);
    
    if(sort == "0"){
       this.userList.sort((a,b) => (a.fullName > b.fullName) ? 1 : ((b.fullName > a.fullName) ? -1 : 0));
       this.dataSource = new MatTableDataSource<PeriodicElement>(this.userList);
       this.dataSource.paginator = this.paginator;
    }else{
     this.userList.sort((a,b) => (a.fullName < b.fullName) ? 1 : ((b.fullName < a.fullName) ? -1 : 0));
     this.dataSource = new MatTableDataSource<PeriodicElement>(this.userList);
     this.dataSource.paginator = this.paginator;
    }
 }
  // sortfilter(event) {
  //   let sort = event.target.value;
  //   console.log(sort)

  //   if (sort == "0") {
  //     this.userList.sort((a, b) => (a.fullName.charAt(0).toUpperCase() > b.fullName.charAt(0).toUpperCase()) ? 1 : ((b.fullName > a.fullName) ? -2 : -1));
  //     this.dataSource = new MatTableDataSource<PeriodicElement>(this.userList);
  //     this.dataSource.paginator = this.paginator;
  //   } else {
  //     this.userList.sort((a, b) => (a.fullName.charAt(0).toLowerCase() < b.fullName.charAt(0).toLowerCase()) ? 1 : ((b.fullName < a.fullName) ? -2 : -1));
  //     this.dataSource = new MatTableDataSource<PeriodicElement>(this.userList);
  //     this.dataSource.paginator = this.paginator;
  //   }
  // }



  fromDate(event) {
    this.todate = event;
    this.optionsTo = {
      minDate: this.todate,
      maxDate: new Date(),
    };
    console.log(event, "lll");
    this.fromdate = Math.round(new Date(event).getTime());
    console.log("startDate", this.fromdate);
    this.filterDate();
  }

  EndDate(event) {
    console.log(event, "event");
    this.endDate = event;
    this.optionsTo = {
      minDate: this.todate,
      maxDate: new Date(),
    };
    this.endDate = Math.round(new Date(event).getTime());
    console.log("endDate", this.endDate);
    // console.log(new Date(this.endDate * 1000))
    this.filterDate();
  }

  filterDate() {
    console.log(this.fromdate);
    if (this.fromdate && !this.endDate) {
      console.log("hi");
      var fromFilter = new Date(this.fromdate).getTime();
      this.allCoin = this.userList.filter(
        (item: any) => item.createdAt >= fromFilter //s
      );
      this.dataSource = new MatTableDataSource(this.allCoin);
      if (this.forFilter) {
        this.dataSource.filter = this.forFilter.trim().toLowerCase();
      }
      this.dataSource.paginator = this.paginator;
    } else if (this.endDate && !this.fromdate) {
      console.log(this.endDate, "sss");
      var toFilter = new Date(this.todate).getTime() + 86400000;
      this.allCoin = this.userList.filter((item: any) => {
        item.createdAt <= toFilter;
      });
      this.dataSource = new MatTableDataSource(this.allCoin);
      if (this.forFilter) {
        this.dataSource.filter = this.forFilter.trim().toLowerCase();
      }
      this.dataSource.paginator = this.paginator;
    } else {
      var fromFilter = new Date(this.fromdate).getTime();
      var toFilter = new Date(this.endDate).getTime() + 86400000;
      this.allCoin = this.userList.filter(
        (item: any) =>
          item.createdAt <= toFilter && item.createdAt >= fromFilter
      );
      this.dataSource = new MatTableDataSource(this.allCoin);
      if (this.forFilter) {
        this.dataSource.filter = this.forFilter.trim().toLowerCase();
      }
      this.dataSource.paginator = this.paginator;
    }
  }


}
