  sortfilter(event) {
    let sort = event.target.value;
    console.log(sort, "hiiii inside sort");
    if (sort === "0") {
      // console.log(this.packageList,"userss")
      this.packageList.sort((a, b) => {
        //  console.log(a.packageName,b.packageName,"kjshakdjhsajhdsaj");
        a.packageName > b.packageName
          ? 1
          : b.packageName > a.packageName
            ? -1
            : 0;
      });
      this.dataSource = new MatTableDataSource<PeriodicElement>(
        this.packageList
      );
      this.dataSource.paginator = this.paginator;
    } else {
      this.packageList.sort((a, b) =>
        a.packageName < b.packageName
          ? 1
          : b.packageName < a.packageName
            ? -1
            : 0
      );
      this.dataSource = new MatTableDataSource<PeriodicElement>(
        this.packageList
      );
      this.dataSource.paginator = this.paginator;
    }
  }

