  singleFilesForCoin1(event) {
    this.coinLookChange = true
    console.log("in singleFilesForCoin");
    const file1 = event.target.files[0];

    this.single = [];
    var singleFiles = event.target.files;
    if (singleFiles) {
      for (var file of singleFiles) {
        // console.log(file, "files br");
        var singleReader = new FileReader();
        singleReader.onload = (e: any) => {
          this.single.push(e.target.result);

          document
            .getElementById("img5") //coinlook
            .setAttribute("src", e.target.result);
        };
        singleReader.readAsDataURL(file);
      }
    }
    this.coinLookPicture = event.target.files[0];
    console.log(this.coinLookPicture, "::::::::::::::::::::::::::;image");
  }
