  editcurrency() {
    // console.log(
    //   this.coin_id,
    //   this.coinsValue,
    //   this.ediCurrencyForm.value.coinSymbol,
    //   "inside editcurrency:::::::"
    // );
    let cData = this.coinsValue
    console.log(cData, "vjhvvvvvvvv644444747474vvjvhm")
    let editdata = new FormData();
    editdata.append("coinID", this.ediCurrencyForm.value.coinID);
    editdata.append("coinName", this.ediCurrencyForm.value.coinName);
    editdata.append("coinLook",
      this.coinLookChange == true ? this.coinLookPicture : cData.coinLook
    );
    editdata.append("coinSymbol",
      this.coinSymbolChange == true ? this.coinSymbolImage : cData.coinSymbol
    );
    editdata.append(
      "itemPurchasedThroughCoin",
      this.selectpCriteriaonEdit == true ? JSON.stringify(this.canPurchaseArray) : JSON.stringify(cData.itemPurchasedThroughCoin)
    );
    editdata.append(
      "purchaseCriteria",
      this.pCriteriaChange == true ? this.selectedpCriteria : cData.purchaseCriteria
      // this.ediCurrencyForm.value.purchaseCriteria
    );
    editdata.append("coin_id", this.coin_id);


    this.api.editcurrency(editdata).subscribe(
      (editdata) => {
        console.log(editdata, "after editcurrency api");
        this.ediCurrencyForm.reset();
        this.getAllCoins();
      },
      (error) => {
        console.log(error);
        //  this.invalidPassWord=true
        this.errorMessage = error.error.message
        //   // this.toastr.error(JSON.stringify(error['error']['message']));
        //   console.log(this.errorMessage )

        //   return this.router.navigateByUrl('/home/profile');
      }
    );
  }
