import { Component, OnInit, ViewChild } from '@angular/core';
import { MatPaginator, MatSort, MatTableDataSource } from "@angular/material";
import { AuthenticationService } from "../../service/authentication.service";
import { DomSanitizer } from '@angular/platform-browser';
import { ToastrService } from 'ngx-toastr';


export interface PeriodicElement {
  usersid:string;
  usertask:string;
  userreward: string; 
}
export interface PeriodicElement1 {
  roomsid:string;
  roomtask:string;
  roomreward: string;
}
export interface PeriodicElement2 {
  momentsid:string;
  momenttask:string;
  momentreward: string;
}

const ELEMENT_DATA: PeriodicElement[] = [
  { usersid:"1", usertask:"Take the Mic for 10 minutes", userreward: "10 Silver Coin" },
  { usersid:"2", usertask:"Send Gifts in the Room", userreward: "10 Silver Coin" },
  { usersid:"3", usertask:"Share room on social Media plateforms", userreward: "10 Silver Coin" },
];
const ELEMENT_DATA1: PeriodicElement1[] = [
  { roomsid:"1",roomtask:"Create a Room", roomreward: "10 Silver Coins"},
  { roomsid:"2",roomtask:"Grab a Mic For 10 Minutes", roomreward: "10 Silver Coins"},
  { roomsid:"3",roomtask:"Share room on social Media plateforms", roomreward: "10 Silver Coins"},
  { roomsid:"4",roomtask:"Add 10 Friends", roomreward: "10 Silver Coins"},
];
const ELEMENT_DATA2: PeriodicElement2[] = [
  { momentsid:"1", momenttask:"Sign In", momentreward: "10 Silver Coins"},
  { momentsid:"2", momenttask:"Sign In", momentreward: "3 Gold Coins"},
  { momentsid:"3", momenttask:"Sign In", momentreward: "10 Silver Coins"},
  { momentsid:"4", momenttask:"Sign In", momentreward: "5 Gold Coins"},
  { momentsid:"5", momenttask:"Sign In", momentreward: "10 Silver Coins"},
  { momentsid:"6", momenttask:"Sign In", momentreward: "10 Silver Coins"},
  { momentsid:"7", momenttask:"Sign In", momentreward: "10 Silver Coins"},
];


@Component({
  selector: 'app-reports-management',
  templateUrl: './reports-management.component.html',
  styleUrls: ['./reports-management.component.css']
})
export class ReportsManagementComponent implements OnInit {

  reportedRoom
  reportedUser
  reportedMoment
  reportDetail
  paginator
  paginator1
  paginator2

  displayedColumns: string[] = ["usercheckbox","usertask","userreward","useraction"];
  displayedColumns1: string[] = ["roomcheckbox","roomtask","roomreward","roomaction"];
  displayedColumns2: string[] = ["momentcheckbox","momenttask","momentreward","momentaction"];
  
  dataSource = new MatTableDataSource<any>(ELEMENT_DATA);
  dataSource1 = new MatTableDataSource<any>(ELEMENT_DATA1);
  dataSource2 = new MatTableDataSource<any>(ELEMENT_DATA2);  

  public doFilter = (value: string) => {
    this.dataSource.filter = value.trim().toLocaleLowerCase();
  };
  @ViewChild('firstPaginator', {static: true}) firstPaginator: MatPaginator;
  @ViewChild('secondPaginator', {static: true}) secondPaginator: MatPaginator;
  @ViewChild('thirdPaginator', {static: true}) thirdPaginator: MatPaginator;
  @ViewChild(MatSort, { static: true }) sort: MatSort;

  constructor( 
    private api : AuthenticationService,
    private sanitizer: DomSanitizer,
    private toast: ToastrService
  ) { }

  ngOnInit() {
    this.getAllReportUser()
    this.getAllReportRoom()
    this.getAllReportMoment()
  }

  

  getAllReportRoom(){
    this.api.getReports({reportFor:1}).subscribe( data => {
          this.reportedRoom = data['response']
          if( this.reportedRoom.length > 0 ){
            for( let x=0; x<this.reportedRoom.length; x++ ){
              this.reportedRoom[x].type = 1
            }
          }
          console.log('room' , this.reportedRoom)
          this.dataSource1 = new MatTableDataSource<PeriodicElement>(this.reportedRoom);
          this.dataSource1.paginator = this.secondPaginator;
          this.dataSource1.sort = this.sort;
    } )
  }

  getAllReportUser(){
    this.api.getReports({reportFor:2}).subscribe( data => {
      this.reportedUser = data['response']
      if( this.reportedUser.length > 0 ){
        for( let x=0; x<this.reportedUser.length; x++ ){
          this.reportedUser[x].type = 2
        }
      }
      console.log('user' , this.reportedUser)
      this.dataSource = new MatTableDataSource<PeriodicElement>(this.reportedUser);
      this.dataSource.paginator = this.firstPaginator;
      this.dataSource.sort = this.sort;
    } )
  }

  getAllReportMoment(){
this.api.getReports({reportFor:3}).subscribe( data => {
  this.reportedMoment = data['response']
  if( this.reportedMoment.length > 0 ){
    for( let x=0; x<this.reportedMoment.length; x++ ){
      this.reportedMoment[x].type = 3
    }
  }
  console.log('moment' , this.reportedMoment)
  this.dataSource2 = new MatTableDataSource<PeriodicElement>(this.reportedMoment);
  this.dataSource2.paginator = this.thirdPaginator;
  this.dataSource2.sort = this.sort;
} )
  }

  ownerName
  getDetail( element , type ){
    this.reportDetail = element
    this.reportDetail.tabFor = `${type}`
    console.log(this.reportDetail)
    if( this.reportDetail.room_id ){
      if( this.reportDetail.room_id == null ){
        this.reportDetail.room_id = "Room Deleted"
      }
      this.ownerName = `${this.reportDetail.room_id.user_id.fullName}`
    }
    if( !this.reportDetail.room_id ){
      this.reportDetail.room_id = false
    }
    if( !this.reportDetail.video ){
      this.reportDetail.video = []
    }
    if( !this.reportDetail.images ){
      this.reportDetail.images = []
    }

    
  }

  sureDelete(){
    this.api.deleteReport({deleteFor:this.reportDetail.type,reportId:this.reportDetail._id}).subscribe( data => {
      this.getAllReportUser()
      this.getAllReportRoom()
      this.getAllReportMoment()
    } )

  }

  videoUrl(){
    // return this.sanitizer.bypassSecurityTrustResourceUrl("https://voicechatimages.s3.me-south-1.amazonaws.com/1615646787559/messagevideo.mp4");
  }

  userAlertButton( type , typeNum){
    this.api.userReportType({reportType:typeNum , userId:this.reportDetail.reported_user_id._id}).subscribe( result => {
      this.toast.success("Success" , `User ${type}`)
    } )
  }
  

}
