import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { AuthenticationService } from '../../service/authentication.service';
declare var $: any;

@Component({
  selector: 'app-room-management-view',
  templateUrl: './room-management-view.component.html',
  styleUrls: ['./room-management-view.component.css']
})
export class RoomManagementViewComponent implements OnInit {

  constructor(
    private api: AuthenticationService,
    public route:ActivatedRoute,
  ) { }
  roomId
  room
  ngOnInit() {    
    this.roomId = this.route.snapshot.paramMap.get('id');
    this.getUserById(this.roomId)
    console.log(this.roomId, "jhbkjvkjvjl")
    $('.nav-tabs li a').on('click', function(){
      $('.nav-tabs li').removeClass('active');
      $(this).addClass('active');
    });
    
  }
  getUserById(id){
    let data={
      id:id
    }
    this.api.getRoomById(data).subscribe(res=>{
      this.room = res['roomDetail'];
      console.log(this.room, "jhvkcvkcv75765r76476776373753")
    },err=>{
      console.log(err)
    })



    
  }



















}
