import { Component, OnInit } from "@angular/core";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { Router, ActivatedRoute } from "@angular/router";
import { AuthenticationService } from "../../service/authentication.service";
import * as CKEditor from '@ckeditor/ckeditor5-build-classic';


declare var $: any;
@Component({
  selector: "app-cms-management",
  templateUrl: "./cms-management.component.html",
  styleUrls: ["./cms-management.component.css"],
})
export class CmsManagementComponent implements OnInit {
  addFaqQuestion: FormGroup;
  editFaq: FormGroup;
  submitted = false;
  faqId: any;
  public Editor = CKEditor;


  constructor(
    private service: AuthenticationService,
    private router: Router,
    public route: ActivatedRoute,
    private formBuilder: FormBuilder
  ) {}
  admin;
  ngOnInit() {
    this.admin = JSON.parse(localStorage.getItem("currentUser"));
    this.getFaq();
    this.getSetting();
    // this.addFaq();

    this.addFaqQuestion = this.formBuilder.group({
      question: ["", [Validators.required]],
      answer: ["", [Validators.required]],
    });

    this.editFaq = this.formBuilder.group({
      question: ["", [Validators.required]],
      answer: ["", [Validators.required]],
    });
  }

  //Form control for adding data i.e FAQ
  get f() {
    return this.addFaqQuestion.controls;
  }

  //Form control for editing data i.e FAQ
  get g() {
    return this.editFaq.controls;
  }

  //Adding data to FAQ
  addFaq() {
    this.submitted = true;
    if (this.addFaqQuestion.invalid) {
      return;
    }

    var data = {
      question: this.addFaqQuestion.value.question,
      answer: this.addFaqQuestion.value.answer,
    };

    this.service.addFaqQuestion(data).subscribe(
      (data) => {
        console.log("Value posted", data);
        this.service.toaster("success", "Successfully added");
        this.router.navigateByUrl("/management/cms-management");
        this.getFaq();
        this.addFaqQuestion.reset();
        $("#addfaqs").modal("hide");
      },
      (error) => {
        console.log(error);
        this.service.toaster("error", "Something went wrong");
      }
    );
  }

  //Listing the data of FAQ
  get: any;
  getFaq() {
    this.service.getAllFaq().subscribe((res: any) => {
      this.get = res.response;
      console.log("Listing all FAQ data", this.get);
    });
  }

  // Deleting data by id of FAQ
  id;
  getElementId(id) {
    this.id = id;
    console.log(this.id, "this is Id");
  }

  delFaq() {
    this.service.delFaq({ faqId: this.id }).subscribe((res) => {
      console.log("This is delete faq data", res);
      this.getFaq();
    });
  }

  //Editing data by id of FAQ
  editFaqQuestion( val) {

    this.submitted = true;
    if (this.editFaq.invalid) {
      return;
    }

    let data = this.editFaq.value;
    data.faqId = this.faqDetail._id;

    this.service.editFaq(data).subscribe((res:any) => {
      console.log("This is edit Faqquestion", res);
      this.editFaq.reset();
      this.getFaq();

      $("#editfaqs").modal("hide");
    });
  }

  faqDetail;
  editFaqQues(element) {
    this.faqDetail = element
    this.editFaq.patchValue(element);
  }

  //Getting data of Setting
  data: any;
  getSetting() {
    this.service.getSettings().subscribe((res: any) => {
      this.data = res.response;
      console.log("Listing all setting data", this.data);
      setTimeout(() => {
        $("#deleted-success").modal("hide");
      }, 1200);
    });
  }

  //updating data of Setting
  saveSetting(value) {
    let data = this.data[0];

    switch (this.modalValue) {
      case 1:
        data.aboutUs = this.textValue;
        break;
      case 2:
        data.termCondition = this.textValue;
        break;
      case 3:
        data.contactUs = this.textValue;
        break;
      case 4:
        data.legal = this.textValue;
        break;
      case 5:
        data.privacyPolicy = this.textValue;
        break;
    }
    this.service.saveSettings(data).subscribe((res: any) => {
      console.log("this is saveSetting data", res);
      $("#close-modal").click();
      this.getSetting();
    });
  }

  textValue;
  modalValue;
  getEditValue(element) {
    this.modalValue = element;
    switch (element) {
      case 1:
        this.textValue = this.data[0].aboutUs;
        break;
      case 2:
        this.textValue = this.data[0].termCondition;
        break;
      case 3:
        this.textValue = this.data[0].contactUs;
        break;
      case 4:
        this.textValue = this.data[0].legal;
        break;
      case 5:
        this.textValue = this.data[0].privacyPolicy;
        break;
    }
  }
}
