import { Component, OnInit, ViewChild } from "@angular/core";
import { MatPaginator, MatSort, MatTableDataSource } from "@angular/material";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { AuthenticationService } from "../../service/authentication.service";
import { Router, ActivatedRoute } from "@angular/router";
import { ToastrService } from 'ngx-toastr';

declare var $: any;
export interface PeriodicElement {
  cityid: string;
  cityname: string;
  country: string;
}

const ELEMENT_DATA: PeriodicElement[] = [
  {
    cityid: "32",
    cityname: "Noida",
    country: "india",
  },
];

@Component({
  selector: "app-city-management",
  templateUrl: "./city-management.component.html",
  styleUrls: ["./city-management.component.css"],
})
export class CityManagementComponent implements OnInit {
  cityName: any = "";
  displayedColumns: string[] = [
    // "checkbox",
    "cityid",
    "cityname",
    "country",
    "action",
  ];

  dataSource = new MatTableDataSource<PeriodicElement>(ELEMENT_DATA);
  public doFilter = (value: string) => {
    this.dataSource.filter = value.trim().toLocaleLowerCase();
  };

  @ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
  @ViewChild(MatSort, { static: true }) sort: MatSort;
  roomid: any;
  invalidLogin: boolean;
  city_id: any;
  inputValue: string;
  errorMessage: any;
  inputblank: string;
  filterName: string;
  cityList: any;
  nocityname: boolean;
  countryList: any;
  countryArr: [];

  constructor(
    private formBuilder: FormBuilder,
    private api: AuthenticationService,
    private router: Router,
    private route: ActivatedRoute,
    private toast: ToastrService
  ) { }
  admin
  ngOnInit() {
    this.admin = JSON.parse(localStorage.getItem('currentUser'))
    this.getAllCity();
    this.getAllCountry();
    this.dataSource.paginator = this.paginator;
    this.dataSource.sort = this.sort;
  }

  getAllCountry() {
    this.api.getAllCountry().subscribe(
      (res) => {
        this.countryArr = [];
        // console.log(res,":::::::::::::::::::countryList");
        // this.dataSource = new MatTableDataSource(res['response']);
        // console.log(this.dataSource,"lll")
        this.countryList = res["countryList"]
        console.log(this.countryList, ":::::::::::::::::::::::countryList");
        //  this.countryList.forEach(elem=> {
        //    this.countryArr.push(elem);
        //  });
        // this.dataSource = new MatTableDataSource(res['countryList']);
        // this.dataSource.paginator = this.paginator;
        // this.dataSource.sort = this.sort;
      },
      (err) => {
        console.log(err);
      }
    );
  }


  getAllCity() {
    this.api.getAllCity().subscribe(
      (res) => {
        console.log(res, ":::::::::::::::::::after api");
        // this.dataSource = new MatTableDataSource(res['response']);
        console.log(this.dataSource, "lll");
        this.cityList = res["cityList"];
        this.dataSource = new MatTableDataSource(res["cityList"]);
        this.dataSource.paginator = this.paginator;
        this.dataSource.sort = this.sort;
        console.log('dddd', this.cityList)

      },
      (err) => {
        console.log(err);
      }
    );
  }

  getElementId(id) {
    this.city_id = id;
    console.log(this.city_id, "roomid");
  }

  clear(inputValue) {
    console.log("clear")
    this.inputValue = null;
    console.log(":::::::::", this.inputValue, "hi")
  }



  delCity() {
    var datadelete = {
      id: this.city_id,
    };

    this.api.delCity(datadelete).subscribe(
      (d) => {
        // console.log(this.deletesub,'data')
        console.log(d, "data here is:::::::::::::::::::::::::::::::: ");
        if (d) {
          this.getAllCity();
        }
        setTimeout(() => {
          $("#deleted-success").modal("hide");
        }, 1200);
      },
      (error) => {
        //console.log(error);
        this.invalidLogin = true;
        //this.errorMessage = error.error.message
        // this.toastr.error(JSON.stringify(error['error']['message']));
        console.log(error);
      }
    );
  }
  onKey(event) {
    this.inputValue = event.target.value;
    console.log(this.inputValue, "input::::::::::::::::::::");
  }

  onsubmit() {                                        //addcity
    console.log(this.inputValue, "on submit", this.cityName);
    if (this.cityName === "" || this.cityName === null) {
      this.nocityname = true;
      return
    }
    let data = {
      cityName: this.cityName
    }
    this.api.addCity(data).subscribe(
      (data) => {
        this.api.toaster("success", "City Added Successfully", 900);
        //     setTimeout(() => {
        //       this.api.toaster("success","City Added Successfully.");
        //  }, 1000);
        $('#addcity').modal('hide');
        console.log(data, "after api");
        this.cityName = null;
        this.getAllCity();
      }, error => {
        console.log(error);
        this.errorMessage = error.error.message
        this.api.toaster("error", this.errorMessage)
        // this.toastr.error(JSON.stringify(error['error']['message']));
        console.log(this.errorMessage)

        // return this.router.navigateByUrl('/home/profile'); 
      })
  }


  blockunblockCity() {
    this.api.blockunblockCity({ id: this.city_id }).subscribe(
      (res) => {
        console.log(res, "After api hit");
        this.getAllCity();
        if (res["messsage"] = "city enabled successfully") {
          setTimeout(() => {
            $("#unsuccess-block").modal("hide");
          }, 900);
        }
        if (res["message"] = "city disabled successfully") {
          setTimeout(() => {
            $("#success-block").modal("hide");
          }, 900);
        }
      },
      (error) => {
        console.log(error);
      }
    );
  }


  sortfilter(event) {
    let sort = event.target.value;
    console.log(sort, "hiiii inside sort")
    if (sort === "0") {
      console.log(this.cityList, "userss")
      this.cityList.sort((a, b) => (a.cityName > b.cityName) ? 1 : ((b.cityName > a.cityName) ? -1 : 0));
      this.dataSource = new MatTableDataSource<PeriodicElement>(this.cityList);
      this.dataSource.paginator = this.paginator;
    } else {
      this.cityList.sort((a, b) => (a.cityName < b.cityName) ? 1 : ((b.cityName < a.cityName) ? -1 : 0));
      this.dataSource = new MatTableDataSource<PeriodicElement>(this.cityList);
      this.dataSource.paginator = this.paginator;
    }
  }


}
