import { Component, OnInit } from '@angular/core';
import { MatPaginator, MatSort, MatTableDataSource } from "@angular/material";
import { ViewChild } from '@angular/core';
import { AuthenticationService } from '../../service/authentication.service';

export interface PeriodicElement {
  sid:string;
  name: string;
  duration: string; 
}
const ELEMENT_DATA: PeriodicElement[] = [
  {
    sid:"101",
    name: "Hydrogen",
    duration: "Time Period",   
  },
];

@Component({
  selector: 'app-feedback-management',
  templateUrl: './feedback-management.component.html',
  styleUrls: ['./feedback-management.component.css']
})
export class FeedbackManagementComponent implements OnInit {
  allFeedback
  myReply : string

  displayedColumns: string[] = [
    "checkbox",
    "sid",
    "name",
    "duration",
    "action",
  ];
  dataSource = new MatTableDataSource<PeriodicElement>(ELEMENT_DATA);
  public doFilter = (value: string) => {
    this.dataSource.filter = value.trim().toLocaleLowerCase();
  };
  @ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
  @ViewChild(MatSort, { static: true }) sort: MatSort;

  constructor(
    private api:AuthenticationService
  ) { }

  ngOnInit() {
    this.getAllFeedbackList()
  }

  getAllFeedbackList(){
    this.api.getAllFeedback().subscribe( data => {
      this.allFeedback = data['data']
      this.dataSource = new MatTableDataSource<PeriodicElement>(this.allFeedback);
      this.dataSource.paginator = this.paginator;
      this.dataSource.sort = this.sort;
      console.log( this.allFeedback )
    } )
  }

  feedbackId
  feedbackDetail
  getId(elem){
    console.log( elem )
    this.feedbackId = elem._id
    this.feedbackDetail = elem
    this.myReply = elem.reply
  }

  sureDelete(){
    this.api.deleteFeedback({feedbackId:this.feedbackId}).subscribe( data => {
      this.getAllFeedbackList()
    } )
  }

  sendReply(){
    this.api.sendAdminReply({feedbackId:this.feedbackId,reply:this.myReply}).subscribe( data => {
      this.getAllFeedbackList()
    })
  }

}
