  blockunblockMembership() {
    this.api.blockunblockMembership({ id: this.membershipId }).subscribe(
      (res) => {
        this.getAllMembershipList();
        if(res["messsage"] = "Membership enabled successfully"){
          setTimeout(() => {
            $("#unsuccess-block").modal("hide");
          }, 900);
        }
        if(res["message"] = "Membership disabled successfully"){
          setTimeout(() => {
            $("#success-block").modal("hide");
          }, 900);
        }
      },
      (error) => {
        console.log(error);
      }
    )
  };
  deleteMembership() {
    var datadelete = {
      packageId: this.membershipId,
      apiType: 1
    };
    this.api.actionPackages(datadelete).subscribe(
      (d) => {
        if (d) {
          this.getAllMembershipList();
        }
        setTimeout(() => {
          $("#deleted-success").modal("hide");
        }, 1200);
      },
      (error) => {
        console.log(error);
      }
    );
  }

  sortfilter(event){
    let sort = event.target.value;
    if(sort === "1"){
      console.log(this.allMembershipList)
       this.allMembershipList.sort((a,b) => (a.membershipName > b.membershipName) ? 1 : ((b.membershipName > a.membershipName) ? -1 : 0));
       this.dataSource = new MatTableDataSource<PeriodicElement>(this.allMembershipList);
       this.dataSource.paginator = this.paginator;
    } 
    if(sort === "2"){
     this.allMembershipList.sort((a,b) => (a.membershipName < b.membershipName) ? 1 : ((b.membershipName < a.membershipName) ? -1 : 0));
     this.dataSource = new MatTableDataSource<PeriodicElement>(this.allMembershipList);
     this.dataSource.paginator = this.paginator;
    }
  }
