  getCheckboxValue(event) {
    this.incType = true
    console.log( 'checkingggggg' , event.target )
    const checkvalue = event.target.id;
    if (event.target.checked) {   
      this.newArray.push(checkvalue);
      console.log(this.newArray, "array");
    } else {
      let removeIndex = this.newArray.findIndex((a) => {
        return a === checkvalue;
      });
      if (removeIndex !== -1) {
        this.newArray.splice(removeIndex, 1);
      }
    }
    this.newArray = JSON.parse( JSON.stringify( this.newArray ) )
  }
