  updateMemberShip(){
    if(this.newArray.length < 0) {
      this.api.toaster("error","Pease select at least 1 includes",900)
      return
    }
    if(this.membershipEditForm.invalid) return;
    let editdata = {
      packageId: this.mId,
      apiType: 2,
      membershipName: this.membershipEditForm.value.memberShip_name,
      membershipDuration: this.membershipEditForm.value.memberShip_duration,
      priceType: this.pType == true ? this.priceT  : this.selectPriceType,
      price: this.membershipEditForm.value.priceValue,
      include: this.incType == true ? this.newArray  : this.selmIncludes
    }
    this.api.actionPackages(editdata).subscribe(
      (abc)=>{
        this.api.toaster("success","Membership Updated Successfully",900) //toaster 
        this.getAllMembershipList();
        $('#membershipedit').modal('hide');
      this.getAllMembershipList();
      this.newArray=[];
      this.membershipEditForm.reset();
      },
      error=> {
        console.log(error);
         this.api.toaster("error",error.error.message)
      })

  }
