import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { AuthenticationService } from '../../service/authentication.service';
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
@Component({
  selector: 'app-room-management-edit',
  templateUrl: './room-management-edit.component.html',
  styleUrls: ['./room-management-edit.component.css']
})
export class RoomManagementEditComponent implements OnInit {
  single: any[];
  picture: any;
room: any;
roomId: any;
editroomForm:FormGroup;

  constructor(private router: Router,
    private formBuilder: FormBuilder,   
    public route:ActivatedRoute,
    private api: AuthenticationService) { }

  ngOnInit() {
    this.roomId = this.route.snapshot.paramMap.get('id');
    console.log(this.roomId, "jhbkjvkjvjl")
    this.getUserById(this.roomId)
    this.editroomForm = this.formBuilder.group({
      Title: ['', Validators.required],
      Country: ['', Validators.required],
      Announcement:['',Validators.required]
    });
  }
  pChange
  singleFiles1(event) {
    this.pChange = true
    const file1 = event.target.files[0]
    this.single = [];
    var singleFiles = event.target.files;
    if (singleFiles) {
      for (var file of singleFiles) {
        var singleReader = new FileReader();
        singleReader.onload = (e:any) => {
          this.single.push(e.target.result);
          document.getElementById("imagefile1").setAttribute('src',e.target.result);
        }
        singleReader.readAsDataURL(file);
      }
    }
    this.picture = event.target.files[0];
    console.log(this.picture,"::::::::::::::::::::::::::;singleFiles1 brooooooo");
  }
  get g(){
    return this.editroomForm.controls;
  }

  getUserById(id){
    let data={
      id:id
    }
    
    console.log(this.roomId)
    this.api.getRoomById(data).subscribe(res=>{
      console.log(res, "jhvkcvkcv75765r76476776373753")
      this.room = res['roomDetail'];
      console.log(this.room, "this.room")
      this.editroomForm.patchValue({ 
        Title: this.room.roomTitle,
        Country : this.room.country,
        Announcement: this.room.announcement
      }); 
    },err=>{
      console.log(err)
    })
  }
  submitted
  editRoom(){
    this.submitted = true;
    console.log(this.editroomForm.invalid,"tur",this.editroomForm.controls, this.room._id)
      if(this.editroomForm.invalid){
        return;
      } 
      let a = new FormData();
      a.append('room_id',this.room._id)
      a.append('roomTitle',this.editroomForm.value.Title)
      a.append('announcement',this.editroomForm.value.Announcement)
      a.append('country',this.editroomForm.value.Country)
      a.append('roomImage',   this.pChange ==true ? this.picture :  this.room.roomImage )
      this.api.editRoom(a).subscribe(
        (editdata) => {
          console.log(editdata, "after editcurrency api");
          this.editroomForm.reset();
          this.api.toaster("success","Updated Successfully.")
          this.router.navigateByUrl('/management/room-management');
        },
        (error) => {
          console.log(error);
          this.api.toaster("error","Something went wrong.")
        }
      );


  }


  
}
