import { Component, OnInit } from '@angular/core';
import { ChatService } from '../support-management/chat.service';
import { AuthenticationService } from '../../service/authentication.service';
import { FormBuilder, FormGroup, Validators } from "@angular/forms";


declare var $: any;

@Component({
  selector: 'app-support-management',
  templateUrl: './support-management.component.html',
  styleUrls: ['./support-management.component.css']
})
export class SupportManagementComponent implements OnInit {
  allUser
  showChat = false
  selectedUser
  myMessage: FormGroup;
  adminDetail
  filteredData :any
  searchText : string
  paragraph =<any> [];
  chatAdmin =<any> [];

  constructor(
    private chatService: ChatService,
    private api: AuthenticationService,
    private fromBuilder: FormBuilder
  ) { }

  ngOnInit() {
    this.adminDetail =  JSON.parse(localStorage.getItem('currentUser'));
    console.log(this.adminDetail) 
    this.getAllUser()
    console.log('now working')
    this.chatService.getMessagesList().subscribe((result) => {
      console.log('success..............', result)
    }, (error) => {
      console.log('failed...........', error)
    })

    this.getUnreadMessages()

    this.myMessage = this.fromBuilder.group({
      messageText: ["",[Validators.required]]
    });
  }

  getAllUser(){
    this.api.getAllUserList().subscribe( ( result ) => {
      this.allUser = result['response']
      this.filteredData = result['response']
      if( this.allUser.length > 0 ){
        for( let x=0; x<this.allUser.length; x++ ){
      this.paragraph.push( {fullName:`${this.allUser[x].fullName}`, profileImage:`${this.allUser[x].profileImage}` , _id:`${this.allUser[x]._id}`})
        }
      }
    } )
  }

  getUnreadMessages(){
    setInterval( async () => { 
        this.chatService.getUnreadMessage().subscribe((result) => {
          if( result.length > 0 ){
            for( let x=0; x< result.length ; x++ ){
              if( result[x]['userId'] ){
                let findIndex = this.paragraph.findIndex((a) => {
                  return `${a._id}` == `${result[x]['userId']}`;
                });

                if( result[x]['unReadMsgCount'] > 0 ){
                  if( this.selectedUser != null || this.selectedUser != undefined ){
                    if( `${this.selectedUser._id}` == `${result[x]['userId']}` ){
                      result[x]['unReadMsgCount'] = 0
                    }
                  }
                  // this.filteredData[findIndex].unRead = result[x]['unReadMsgCount']
                  this.filteredData.splice(findIndex,1);
                  this.filteredData.unshift({fullName:result[x]['fullName'] , profileImage:result[x]['profileImage'],_id:`${result[x]['userId']}`,unRead:result[x]['unReadMsgCount']});
                  
                  // this.paragraph[findIndex].unRead = result[x]['unReadMsgCount']
                  this.paragraph.splice(findIndex,1);
                  this.paragraph.unshift({fullName:result[x]['fullName'] , profileImage:result[x]['profileImage'],_id:`${result[x]['userId']}`,unRead:result[x]['unReadMsgCount']});
          
              }

              }
            }
          }
        }, (error) => {
        })
    }, 3000);
  }

  sendMessage(){
    if( this.myMessage.value.messageText == "" || this.myMessage.value.messageText == null || this.myMessage.value.messageText == undefined ){
      return 
    }
    this.chatService.sendMessage( this.selectedUser , this.myMessage.value.messageText , `${this.selectedUser._id}`)
    this.myMessage.value.messageText = ""
    $('.sendInput').val("")
    var chatLength = $('.textMessage').length
    var dataToNotification = {
      user_id:this.selectedUser._id,
      notification_title:`Message from the support`,
      notification_type:4,
      notification_body:`Notification body`
    }
    this.api.sendNotification(dataToNotification).subscribe( res => {
        console.log( 'message sent' , res ) 
    } )



  }

  search(value){
    const regex = value;
    this.filteredData = this.paragraph.filter( data => {
      return data['fullName'].toLowerCase().match(regex.toLowerCase())
    })
  };

  hideChat(){
    this.showChat = false
  }


  userChat
  getUserId( userData ){
    console.log( 'last update build sep 9 -- 11:44 am' )
    this.selectedUser = userData
    if( this.selectedUser._id ){
      this.filteredData.filter( elem => {
        if( `${elem._id}` == `${this.selectedUser._id}` ){
          if( !elem.unRead ){ elem.unRead = 0 }
          else { elem.unRead = 0 }
        }
      } )
    }
    this.showChat = true
    this.chatService.getMessages(userData._id).subscribe( result => {
      this.userChat = result
    })

    this.chatService.updateMessages(userData._id , "unReadMsgCount")

  }

}
