import { Injectable } from '@angular/core';
import { AngularFireDatabase, AngularFireObject } from '@angular/fire/database';


@Injectable({
  providedIn: 'root'
})
export class ChatService {

  constructor(private db: AngularFireDatabase) { }

  getMessagesList() {  
    return this.db.list('messages/PrivateMessages' , ref => {
      return ref.orderByChild('timeStamp');
      }).valueChanges(); 
  }
  getUnreadMessage() {  
    return this.db.list('unReadMsgCountData').valueChanges(); 
  }

  sendMessage(user:any , message:any, chatID:any) {
    const messageData = {
        content: message,
        giftCount:"",
        isDeleted:"",
        mediaType:"text",
        receiverId: user._id,
        receiverName: user.fullName,
        senderId: "vcAdmin",
        senderName: "Abdelrahman Nofal",
        thumnilImageurl: "",
        timeStamp: new Date().getTime(),
        uid: "-MiLQHtRFlYPxFgS9m_C"
    };
    const agentMeta = {
        name: user.name,
        new: true
    };
    const userMeta = {
        new: false
    };
    this.db.list(`messages/PrivateMessages/vcAdmin_${chatID}`).push(messageData);
    // this.db.database.ref(`Chat/${chatID}/meta-data/agent`).update(agentMeta);
    // this.db.database.ref(`Chat/${chatID}/meta-data/user`).update(userMeta);
  }

  getMessages( user:any ){
    return this.db
    .list('messages/PrivateMessages/vcAdmin_' + user , ref => {
    return ref.orderByChild('timeStamp');
    })
    .valueChanges();
  }


  updateMessages( user:any , keyName:string){
    console.log('asasass')
    return this.db.list('unReadMsgCountData/vcAdmin_' + user).set("unReadMsgCount", 0);
  }

}
