import { Component, OnInit, ViewChild } from '@angular/core';
import { MatPaginator, MatSort, MatTableDataSource } from "@angular/material";
import { DatepickerOptions } from "ng2-datepicker";
import { AuthenticationService } from "../../service/authentication.service";
declare var $:any;
export interface PeriodicElement {
  userid:string;
  username: string;
  duration: string;
  date:string;
  postid: string;
  postitle: string; 
}
const ELEMENT_DATA: PeriodicElement[] = [
  { userid:"123456", username: "Name", duration: "Hourly", date:"Date", postid: "123456", postitle: "Name" },
  { userid:"123456", username: "Name", duration: "Hourly", date:"Date", postid: "123456", postitle: "Name" },
];

@Component({
  selector: 'app-posts-management',
  templateUrl: './posts-management.component.html',
  styleUrls: ['./posts-management.component.css']
})
export class PostsManagementComponent implements OnInit {
  allPost:any
  forView:any
  todate:any
  fromdate:any
  selectedItems = []

  displayedColumns: string[] = [
    // "checkbox",
    "userid",
    "username",
    // "duration",
    "date",
    "postid",
    "postitle",    
    "action",
  ];
  dataSource = new MatTableDataSource<PeriodicElement>(ELEMENT_DATA);
  // public doFilter = (value: string) => {
  //   this.dataSource.filter = value.trim().toLocaleLowerCase();
  // };
  single:any[];
  picture:any;

  @ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
  @ViewChild(MatSort, { static: true }) sort: MatSort;

  optionsFrom: DatepickerOptions = {
    minYear: 2017,
    maxDate: new Date(),
  };
  constructor(
    private api:AuthenticationService
  ) { }

  ngOnInit() {
    this.myAllPost()
    console.log('from date' , this.fromdate)
  }

  myAllPost(){
    var currentDate = new Date().getTime()
    this.api.getAllPost().subscribe( ( result ) => {
      this.allPost = result['response'].reverse()
    console.log(this.allPost)

      for( let x=0; x<this.allPost.length; x++ ){
        var finalD = currentDate - new Date(this.allPost[x].createdAt).getTime()
        var Difference_In_Days = parseInt(`${finalD / (1000 * 3600 * 24)}`);
        if( Difference_In_Days <= 0  ){ this.allPost[x].duration = 'Hourly' }
        if( Difference_In_Days >= 0 && Difference_In_Days <= 1 ){ this.allPost[x].duration = 'Daily' }
        if( Difference_In_Days >= 0 && Difference_In_Days <= 7 ){ this.allPost[x].duration = 'Weekly' }
        if( Difference_In_Days >= 0 && Difference_In_Days <= 30 ){ this.allPost[x].duration = 'Monthly' }
        if( this.allPost[x].likes.length >= 0 ){
          this.allPost[x].totalLike = this.allPost[x].likes.length
        }
        if(this.allPost[x].userId){
          this.allPost[x].userName = this.allPost[x].userId.fullName
        }else{
          this.allPost[x].userName  = ""
        }
      }
      this.dataSource = new MatTableDataSource<PeriodicElement>(this.allPost);
      this.dataSource.paginator = this.paginator;
      this.dataSource.sort = this.sort;
    } )

  }

  viewPost(elem:any){
    debugger
    this.forView = elem    
  }

  filterByDate(eve:any){
    if( eve.target.value == 'h' ){
     var filterData =  this.allPost.filter((elem)=>{
        if( elem.duration == 'Hourly' ){
          return elem
        }
      })
    }
    if( eve.target.value == 'd' ){
      var filterData =  this.allPost.filter((elem)=>{
         if( elem.duration == 'Daily' ){
           return elem
         }
      })
    }
    if( eve.target.value == 'w' ){
      var filterData =  this.allPost.filter((elem)=>{
         if( elem.duration == 'Weekly' ){
           return elem
         }
      })
    }
    if( eve.target.value == 'm' ){
      var filterData =  this.allPost.filter((elem)=>{
         if( elem.duration == 'Monthly' ){
           return elem
         }
      })
    }
    this.dataSource = new MatTableDataSource<PeriodicElement>(filterData);
    this.dataSource.paginator = this.paginator;
    this.dataSource.sort = this.sort;
  }


  showFeautured(check:any){
    if( check.target.checked == true ){
      this.allPost.sort(function(a, b) {
        return a.totalLike - b.totalLike
     })
      this.dataSource = new MatTableDataSource<PeriodicElement>(this.allPost);
      this.dataSource.paginator = this.paginator;
      this.dataSource.sort = this.sort;
    }else{
      this.myAllPost()
    }
  }
  
  blockUnblockText
  blockId
  blockUnblock( elem:any ){
    this.blockId = elem._id
      if( elem.isActive == 1 ){
        this.blockUnblockText = 'Block'
      }
      if( elem.isActive == 0 ){
        this.blockUnblockText = 'Unblock'
      }
  }

  blockunblockuser(){
    this.api.blockUnblockPost({
      _id:this.blockId
    }).subscribe( ( result ) => {
      this.myAllPost()
      console.log(result)
    } )

  }

  sortNormal(eve:any){ 
    debugger
    // if( eve.target.value == "0" ){
    //   this.allPost.sort((a, b) => (a.userId.fullName > b.userId.fullName) ? 1 : -1)
    //   this.dataSource = new MatTableDataSource<PeriodicElement>(this.allPost);
    //   this.dataSource.paginator = this.paginator;
    //   this.dataSource.sort = this.sort;
    //   console.log(this.allPost)
    // }
    // if( eve.target.value == 1 ){
    //   this.allPost.sort((a, b) => (a.userId.fullName < b.userId.fullName) ? 1 : -1)
    //   this.dataSource = new MatTableDataSource<PeriodicElement>(this.allPost);
    //   this.dataSource.paginator = this.paginator;
    //   this.dataSource.sort = this.sort;
    //   console.log(this.allPost)
    // }
    let sort= eve.target.value
    console.log(sort, "hua sort");
    

    if (sort == "0") {
      this.allPost.sort((a, b) => (a.address > b.address) ? 1 : ((b.address > a.address) ? -1 : 0))
      this.dataSource= new MatTableDataSource<PeriodicElement>(this.allPost)
      this.dataSource.paginator= this.paginator
    } else {
      this.allPost.sort((a, b) => (a.address < b.address) ? 1 : (b.address < a.address) ? -1 : 0)
      this.dataSource= new MatTableDataSource<PeriodicElement>(this.allPost)
      this.dataSource.paginator= this.paginator
    }
  }


  singleFilesForPackage(event) {
    // console.log("*********************");
    const file1 = event.target.files[0];

    this.single = [];
    var singleFiles = event.target.files;
    if (singleFiles) {
      for (var file of singleFiles) {
        // console.log(file, "files br");
        var singleReader = new FileReader();
        singleReader.onload = (e: any) => {
          this.single.push(e.target.result);
          document.getElementById("imagefile").setAttribute("src", e.target.result);            
        };
        singleReader.readAsDataURL(file);
      }
    }
    this.picture = event.target.files[0]; 
  }

  doFilter( val ){
    this.dataSource.filter = val.trim().toLocaleLowerCase();
  }


  fromDate( dateValue  ){
    this.fromdate = new Date(dateValue).getTime() 
  }
  toDate( dateValue  ){
    this.todate = new Date(dateValue).getTime() 
    this.allPost.forEach((item, index) => {
      if (item.createdAt >= this.fromdate && item.createdAt <= this.todate) {
          this.selectedItems.push(item);
      }
    });
    this.dataSource = new MatTableDataSource<PeriodicElement>(this.selectedItems);
    this.dataSource.paginator = this.paginator;
    this.dataSource.sort = this.sort;

  }

}
