  sortfilter(event){
    let sort = event.target.value;
    console.log(sort,"hiiii inside sort")
    if(sort === "0"){
      console.log(this.countryList,"userss")
       this.countryList.sort((a,b) => (a.countryName > b.countryName) ? 1 : ((b.countryName > a.countryName) ? -1 : 0));
       this.dataSource = new MatTableDataSource<PeriodicElement>(this.countryList);
       this.dataSource.paginator = this.paginator;
    }else{
     this.countryList.sort((a,b) => (a.countryName < b.countryName) ? 1 : ((b.countryName < a.countryName) ? -1 : 0));
     this.dataSource = new MatTableDataSource<PeriodicElement>(this.countryList);
     this.dataSource.paginator = this.paginator;
    }
 }
