import { Component, OnInit, ViewChild } from "@angular/core";
import { MatPaginator, MatSort, MatTableDataSource } from "@angular/material";
import { AuthenticationService } from "../../service/authentication.service";
import { Router, ActivatedRoute } from "@angular/router";
import { FormGroup, FormBuilder, Validators } from "@angular/forms";

declare var $

export interface PeriodicElement {
  fullName: string;
  profileImage: string;
  roomImage: string;
  roomTitle: string;
  package: string;
}
export interface PeriodicElement1 {
  upgradeid: string;
  packageName: string;
  packageImage: string;
  maxOnlineUsers: string;
  maxRoomAdmin: string;
  maxNoOfMic: string;
  price: string;
}

const ELEMENT_DATA: PeriodicElement[] = [];
const ELEMENT_DATA1: PeriodicElement1[] = [];

@Component({
  selector: "app-upgrade-room-management",
  templateUrl: "./upgrade-room-management.component.html",
  styleUrls: ["./upgrade-room-management.component.css"],
})
export class UpgradeRoomManagementComponent implements OnInit {
  displayedColumns: string[] = [
    // "roomcheckbox",
    "fullName",
    "roomTitle",
    "package",
    "action",
  ];
  displayedColumns1: string[] = [
    "sentcheckbox",
    "packageName",
    "packageImage",
    "maxOnlineUsers",
    "maxRoomAdmin",
    "maxNoOfMic",
    "price",
    "upgradeAction",
  ];

  dataSource = new MatTableDataSource<PeriodicElement>(ELEMENT_DATA);
  dataSource1 = new MatTableDataSource<PeriodicElement1>(ELEMENT_DATA1);

  single: any[];
  picture: any;
  submitted = false;
  addUpgradeRoom: FormGroup


  @ViewChild("MatPaginator", { static: true }) paginator: MatPaginator;
  @ViewChild("MatPaginator1", { static: true }) paginator1: MatPaginator;
  @ViewChild(MatSort, { static: true }) sort: MatSort;

  constructor(
    private service: AuthenticationService,
    private router: Router,
    public route: ActivatedRoute,
    private FormBuilder: FormBuilder

  ) { }

  ngOnInit() {
    this.getUserList()
    this.getAllUpgradedList()



    this.addUpgradeRoom = this.FormBuilder.group({
      packageName: ["", [Validators.required]],
      packageImage: ["", [Validators.required]],
      maxOnlineUser: ["", [Validators.required, Validators.pattern(`^[0-9]+$`)]],
      maxRoomAdmin: ["", [Validators.required, Validators.pattern(`^[0-9]+$`)]],
      maxRoomMember: ["", [Validators.required, Validators.pattern(`^[0-9]+$`)]],
      maxNoOfMic: ["", [Validators.required, Validators.pattern(`^[0-9]+$`)]],
      price: ["", [Validators.required, Validators.pattern(`^[0-9]+$`)]],

    })
  }

  list: any;
  getUserList() {
    this.service.getRoomUserList().subscribe((res: any) => {
      this.list = res.response
      this.dataSource = new MatTableDataSource<PeriodicElement>(this.list)
      this.dataSource.paginator = this.paginator
    });
  }


  data: any
  getAllUpgradedList() {
    this.service.getAllUpgradedList().subscribe((res: any) => {
      this.data = res.response
      this.dataSource1 = new MatTableDataSource<PeriodicElement1>(this.data)
      this.dataSource1.paginator = this.paginator1
    })

  }


  singleFiles1(event) {
    const file1 = event.target.files[0]

    this.single = [];
    var singleFiles = event.target.files;
    if (singleFiles) {
      for (var file of singleFiles) {
        var singleReader = new FileReader();
        singleReader.onload = (e: any) => {
          this.single.push(e.target.result);
          document.getElementById("imagefile1").setAttribute('src', e.target.result);
        }
        singleReader.readAsDataURL(file);
      }
    }
    this.picture = event.target.files[0];
    console.log(this.picture, "::::::::::::::::::::::::::;singleFiles1 brooooooo");
  }


  get f() {
    return this.addUpgradeRoom.controls;
  }

  addRoom() {
    debugger

    this.submitted = true

    if (this.addUpgradeRoom.invalid) {
      return
    }


    let data = new FormData();
    data.append("packageName", this.addUpgradeRoom.value.packageName);

    data.append("packageImage", this.picture);
    data.append("maxOnlineUser", this.addUpgradeRoom.value.maxOnlineUser);
    data.append("maxRoomAdmin", this.addUpgradeRoom.value.maxRoomAdmin);
    data.append("maxRoomMember", this.addUpgradeRoom.value.maxRoomMember);
    data.append("maxNoOfMic", this.addUpgradeRoom.value.maxNoOfMic);
    data.append("price", this.addUpgradeRoom.value.price);



    this.service.addUpgradeRoom(data).subscribe((res) => {
      console.log("Upgrade room", res)

      this.service.toaster("success", "Upgrade added Successfully");
      $('#roomAdd').modal('hide');
      this.picture = undefined;
      this.getUserList();
      this.getAllUpgradedList();
      this.addUpgradeRoom.reset();

    },
      (error) => {
        console.log(error)
        this.service.toaster("error", "Something went Wrong");
      })
  }

  clearForm() {
    this.submitted = false
    console.log("hhiiiiiiiiii");
    this.addUpgradeRoom.reset()
  }


  view: any
  viedUser(element) {
    this.view = element
    console.log(this.view, "vied data")
  }









  del_id: any
  pickupID(id) {
    this.del_id = id
  }

  delUpgrade() {
    var datadelete = {
      packageId: this.del_id,
    };
    this.service.deleteUpgrade(datadelete).subscribe(
      (d) => {
        // console.log(this.deletesub,'data')
        console.log(d, "data here is:::::::::::::::::::::::::::::::: ");
        if (d) {
          this.getAllUpgradedList();
        }
        this.service.toaster("success", "Successfully deleted")
        setTimeout(() => {
          $("#deleted-success").modal("hide");
        }, 1200);
      },
      (error) => {
        console.log(error.error.message)
      }
    );
  }








}
