import { Component, OnInit, ViewChild } from "@angular/core";
import { MatPaginator, MatSort, MatTableDataSource } from "@angular/material";
import { AuthenticationService } from "../../service/authentication.service";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { DatepickerOptions } from "ng2-datepicker";
import { Router, ActivatedRoute } from "@angular/router";
import * as enLocale from "date-fns/locale/en";

declare var $: any;
export interface PeriodicElement {
  roomid: string;
  name: string;
  ownerid: string;
  country: string;
  type: string;
  admin: string;
  members: string;
  revenue: string;
}
const ELEMENT_DATA: PeriodicElement[] = [
  {
    roomid: "32",
    name: "Hydrogen",
    ownerid: "34",
    country: "india",
    type: "Delhi, India",
    admin: "12",
    members: "active",
    revenue: "898",
  },
];
@Component({
  selector: "app-room-management",
  templateUrl: "./room-management.component.html",
  styleUrls: ["./room-management.component.css"],
})
export class RoomManagementComponent implements OnInit {
  displayedColumns: string[] = [
    "checkbox",
    "roomid",
    "name",
    "ownerid",
    "country",
    "type",
    "admin",
    "members",
    "revenue",
    "action",
  ];
  dataSource = new MatTableDataSource<PeriodicElement>(ELEMENT_DATA);
  public doFilter = (value: string) => {
    this.dataSource.filter = value.trim().toLocaleLowerCase();
  };

  @ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
  @ViewChild(MatSort, { static: true }) sort: MatSort;

  fromdate: any;
  todate: Date;
  roomid: any;
  invalidLogin: boolean;
  minDate: any;
  startDate: number;
  endDate: number;
  allRoomList:any;
  allRoomListFilter: any;
  forFilter: String;
  optionsTo: { minDate: Date; maxDate: Date };
  optionsFrom: DatepickerOptions = {
    minYear: 1990,
    maxDate: new Date(),
  };

  allRoom: any;


  constructor(
    private formBuilder: FormBuilder,
    private api: AuthenticationService,
    private router: Router,
    private route: ActivatedRoute
  ) {
    this.fromdate = new Date();
    this.todate = new Date();
  } 
admin
  ngOnInit() {
    this.admin = JSON.parse(localStorage.getItem('currentUser')) 
    this.getAllRoomList();
  }

  getAllRoomList() {
    this.api.getAllRoomList().subscribe(
      (res) => {
        // console.log(res, "::::::::::::::::;:::after api");
        this.allRoomList = res["rooms"];
        console.log(this.allRoomList, "roooom list:::::::::::::::::::::");
        this.dataSource = new MatTableDataSource(res["rooms"]);
        this.dataSource.paginator = this.paginator;
        this.dataSource.sort = this.sort;
      },
      (err) => {
        console.log(err);
      }
    );
  }

  getElementId(id) {
    this.roomid = id;
    console.log(this.roomid, "roomid");
  }

  blockunblockRoom() {
    this.api.blockUnblockRoom({ id: this.roomid }).subscribe(
      (res) => {
        this.getAllRoomList();
        console.log(res, "After api hit");
        if(res["messsage"] = "Unblocked successfully"){
          setTimeout(() => {
            $("#unsuccess-block").modal("hide");
          }, 900);
        }
        if(res["message"] = "Blocked successfully"){
          setTimeout(() => {
            $("#success-block").modal("hide");
          }, 900);
        }
       
      },
      (error) => {
        console.log(error);
      }
    )
  };

  delRoom() {
    var datadelete = {
      id: this.roomid,
    };
    this.api.deleteRoom(datadelete).subscribe(
      (d) => {
        // console.log(this.deletesub,'data')
        console.log(d, "data here is ");
        if (d) {
          this.getAllRoomList();
        }
        setTimeout(() => {
          $("#deleted-success").modal("hide");
        }, 1200);
      },
      (error) => {
        //console.log(error);
        this.invalidLogin = true;
        //this.errorMessage = error.error.message
        // this.toastr.error(JSON.stringify(error['error']['message']));
        console.log(error);
      }
    );
  }

  fromDate(event) {
    this.todate = event;
    this.optionsTo = {
      minDate: this.todate,
      maxDate: new Date(),
    };
    console.log(event, "lll");
    this.fromdate = Math.round(new Date(event).getTime());
    console.log("startDate", this.fromdate);
    this.filterDate();
  }

  EndDate(event) {
    console.log(event, "event");
    this.endDate = event;
    this.optionsTo = {
      minDate: this.todate,
      maxDate: new Date(),
    };
    this.endDate = Math.round(new Date(event).getTime());
    console.log("endDate", this.endDate);
    // console.log(new Date(this.endDate * 1000))
    this.filterDate();
  }

  filterDate() {
    debugger
    console.log(this.fromdate);
    if (this.fromdate && !this.endDate) {
      console.log("hi");
      var fromFilter = new Date(this.fromdate).getTime();
      this.allRoom = this.allRoomList.filter(
        (item: any) => item.createdAt >= fromFilter //s
      );
      this.dataSource = new MatTableDataSource(this.allRoom);
      if (this.forFilter) {
        this.dataSource.filter = this.forFilter.trim().toLowerCase();
      }
      this.dataSource.paginator = this.paginator;
    } else if (this.endDate && !this.fromdate) {
      console.log(this.endDate, "sss");
      var toFilter = new Date(this.todate).getTime() + 86400000;
      this.allRoom = this.allRoomList.filter((item: any) => {
        item.createdAt <= toFilter;
      });
      this.dataSource = new MatTableDataSource(this.allRoom);
      if (this.forFilter) {
        this.dataSource.filter = this.forFilter.trim().toLowerCase();
      }
      this.dataSource.paginator = this.paginator;
    } else {
      var fromFilter = new Date(this.fromdate).getTime();
      var toFilter = new Date(this.endDate).getTime() + 86400000;
      this.allRoom = this.allRoomList.filter(
        (item: any) =>
          item.createdAt <= toFilter && item.createdAt >= fromFilter
      );
      this.dataSource = new MatTableDataSource(this.allRoom);
      if (this.forFilter) {
        this.dataSource.filter = this.forFilter.trim().toLowerCase();
      }
      this.dataSource.paginator = this.paginator;
    }
  }

  sortcountry(event){
    console.log(event.target.value,"event")
    if(event.target.value == "All"){
      console.log("all")
      this.dataSource = new MatTableDataSource(this.allRoomList);
    }else{
      this.allRoom = this.allRoomList.filter((item: any) =>
          item.country == event.target.value
      );
      console.log(this.allRoom,"dd")
      this.dataSource = new MatTableDataSource(this.allRoom);
    }
  }

    sortfilter(event){
      let sort = event.target.value;
      console.log(sort,"hiiii inside sort")
      if(sort === "0"){
        console.log(this.allRoomList,"userss")
         this.allRoomList.sort((a,b) => (a.roomTitle > b.roomTitle) ? 1 : ((b.roomTitle > a.roomTitle) ? -1 : 0));
         this.dataSource = new MatTableDataSource<PeriodicElement>(this.allRoomList);
         this.dataSource.paginator = this.paginator;
      }else{
       this.allRoomList.sort((a,b) => (a.roomTitle < b.roomTitle) ? 1 : ((b.roomTitle < a.roomTitle) ? -1 : 0));
       this.dataSource = new MatTableDataSource<PeriodicElement>(this.allRoomList);
       this.dataSource.paginator = this.paginator;
      }
   }




}
