  editGift() {
    console.log(this.gift_id, "inside edit",);
    this.submitted = true
    console.log(this.editGiftForm.invalid,"tur",this.editGiftForm.controls)
    if(this.editGiftForm.invalid) return;


    let editdata = new FormData();
    editdata.append("giftID", this.editGiftForm.value.giftID);
    editdata.append("giftName", this.editGiftForm.value.giftName);
    editdata.append("giftCategory", this.editGiftForm.value.giftCategory);
    editdata.append("giftPrice",this.editGiftForm.value.giftPrice);
    editdata.append("gift_id",this.gift_id)

    if(this.picture === undefined){
      // console.log("patching value inssert")
      editdata.append("giftImage",this.giftValue.giftImage);
    }else{
      // console.log("inside else::::::::::::::::::::::::::::")
      editdata.append("giftImage",this.picture);
    }
    if(this.picture1 === undefined){
      // console.log("patching value inssert")
      editdata.append("gif",this.giftValue.gif);
    }else{
      // console.log("inside else::::::::::::::::::::::::::::")
      editdata.append("gif",this.picture1);
    }

    this.api.editGift(editdata).subscribe(
      (editdata) => {
        this.api.toaster("success","Edit Successfully",900)
        // console.log(editdata, "afterapiiiiiiiiiiiiiiiiiiiiiiiiiiiiiii");
        $("#editgift").modal("hide");
        this.getAllGifts();
        this.editGiftForm.reset();
        this.picture = undefined;
      },
      (error) => {
        console.log(error);
          this.errorMessage = error.error.message;
          this.api.toaster("error",this.errorMessage,900);
      }
    );
  }

