  saveGift(){
    if(this.picture == '' || this.picture == null ) {
      this.api.toaster("error","Please select gift image",1000);
      return
    }
    if(this.picture == '' || this.picture == null ) {
      this.api.toaster("error","Please select gif",1000);
      return
    }
    this.submitted = true
    console.log(this.addGiftForm.invalid,"tur",this.addGiftForm.controls)
    if(this.addGiftForm.invalid) return;
    // this.click = !this.click;
    let editdata = new FormData();
    console.log(this.addGiftForm.value,"Add form valuessss")
    editdata.append("giftID", this.addGiftForm.value.giftID);
    editdata.append("giftName", this.addGiftForm.value.giftName);
    editdata.append("giftCategory", this.addGiftForm.value.giftCategory);
    editdata.append("giftPrice",this.addGiftForm.value.giftPrice);
    editdata.append('giftImage',this.picture)
    editdata.append('gif',this.picture1)

    this.api.saveGift(editdata).subscribe(
      (editdata) => {
        console.log(editdata,":::::::::::edit datas");
        this.api.toaster("success","Gift save Successfully",1000);
        $('#addgift').modal('hide');
        this.picture = undefined;
        this.getAllGifts();
        this.addGiftForm.reset();
        
      },
      (error) => {
        console.log(error);
          this.errorMessage = error.error.message;
          this.api.toaster("error", this.errorMessage,1000);
      }
    );

  }

