   sortfilter(event){
      let sort = event.target.value;
      console.log(sort,"hiiii inside sort")
      if(sort === "0"){
        console.log(this.allGiftList,"userss")
         this.allGiftList.sort((a,b) => (a.giftName > b.giftName) ? 1 : ((b.giftName > a.giftName) ? -1 : 0));
         this.dataSource = new MatTableDataSource<PeriodicElement>(this.allGiftList);
         this.dataSource.paginator = this.paginator;
      }else{
       this.allGiftList.sort((a,b) => (a.giftName < b.giftName) ? 1 : ((b.giftName < a.giftName) ? -1 : 0));
       this.dataSource = new MatTableDataSource<PeriodicElement>(this.allGiftList);
       this.dataSource.paginator = this.paginator;
      }
   }


   sortgiftbyCategory(event){
    if(event.target.value == "All"){
      this.dataSource = new MatTableDataSource(this.allGiftList);
    }else{
      this.filterGift = [];
      this.allGiftList.forEach(element => {
        if(element.giftCategory === event.target.value){
          this.filterGift.push(element);
        }
      });
    //  console.log(this.filterGift,"filter gift array");
     this.dataSource = new MatTableDataSource(this.filterGift);
    }
  }


   fromDate(event) {
    this.todate = event;
    this.optionsTo = {
      minDate: this.todate,
      maxDate: new Date(),
    };
    console.log(event, "lll");
    this.fromdate = Math.round(new Date(event).getTime());
    console.log("startDate", this.fromdate);
    this.filterDate();
  }

  EndDate(event) {
    console.log(event, "event");
    this.endDate = event;
    this.optionsTo = {
      minDate: this.todate,
      maxDate: new Date(),
    };
    this.endDate = Math.round(new Date(event).getTime());
    console.log("endDate", this.endDate);
    // console.log(new Date(this.endDate * 1000))
    this.filterDate();
  }

  filterDate() {
    console.log(this.fromdate);
    if (this.fromdate && !this.endDate) {
      console.log("hi");
      var fromFilter = new Date(this.fromdate).getTime();
      this.allGift = this.allGiftList.filter(
        (item: any) => item.createdAt >= fromFilter //s
      );
      this.dataSource = new MatTableDataSource(this.allGift);
      if (this.forFilter) {
        this.dataSource.filter = this.forFilter.trim().toLowerCase();
      }
      this.dataSource.paginator = this.paginator;
    } else if (this.endDate && !this.fromdate) {
      console.log(this.endDate, "sss");
      var toFilter = new Date(this.todate).getTime() + 86400000;
      this.allGift = this.allGiftList.filter((item: any) => {
        item.createdAt <= toFilter;
      });
      this.dataSource = new MatTableDataSource(this.allGift);
      if (this.forFilter) {
        this.dataSource.filter = this.forFilter.trim().toLowerCase();
      }
      this.dataSource.paginator = this.paginator;
    } else {
      var fromFilter = new Date(this.fromdate).getTime();
      var toFilter = new Date(this.endDate).getTime() + 86400000;
      this.allGift = this.allGiftList.filter(
        (item: any) =>
          item.createdAt <= toFilter && item.createdAt >= fromFilter
      );
      this.dataSource = new MatTableDataSource(this.allGift);
      if (this.forFilter) {
        this.dataSource.filter = this.forFilter.trim().toLowerCase();
      }
      this.dataSource.paginator = this.paginator;
    }
  }










