import { Component, OnInit, ViewChild } from '@angular/core';
import { MatPaginator, MatSort, MatTableDataSource } from "@angular/material";
import { DatepickerOptions } from "ng2-datepicker";
import { ActivatedRoute } from '@angular/router';
import { AuthenticationService } from '../../service/authentication.service';
import { element } from 'protractor';
declare var $: any;
export interface PeriodicElement {
  orderid: string;
  // ordertype: string;
  date: string;
  time: string;
  transactionid: string;
  // paymentmethod: string;
  amount: string;
}
export interface PeriodicElement1 {
  coinorderid: string;
  // coinordertype: string;
  coindate: string;
  cointime: string;
  cointransactionid: string;
  coinpaymentmethod: string;
  coinamount: string;
}
const ELEMENT_DATA: PeriodicElement[] = [
  {
    orderid: "123456", date: "DDMMYY", time: "HH:MM", transactionid: "123456",
    //  paymentmethod: "Card",
    amount: "100 EGP"
  },
  {
    orderid: "123456", date: "DDMMYY", time: "HH:MM", transactionid: "123456",
    // paymentmethod: "Card", 
    amount: "100 EGP"
  },
];
const ELEMENT_DATA1: PeriodicElement1[] = [
  { coinorderid: "123456", coindate: "DDMMYY", cointime: "HH:MM", cointransactionid: "123456", coinpaymentmethod: "Gold Coin", coinamount: "100 EGP" },
  { coinorderid: "123456", coindate: "DDMMYY", cointime: "HH:MM", cointransactionid: "123456", coinpaymentmethod: "Silver Coin", coinamount: "100 EGP" },
];
@Component({
  selector: 'app-payment-view-user',
  templateUrl: './payment-view-user.component.html',
  styleUrls: ['./payment-view-user.component.css']
})
export class PaymentViewUserComponent implements OnInit {
  allTransaction: any
  moneyTotal
  coinTotal
  todate: any
  fromdate: any
  displayedColumns: string[] = [
    "checkbox",
    "orderid",
    // "ordertype",
    "date",
    "time",
    "transactionid",
    // "paymentmethod",
    "amount",
  ];
  displayedColumns1: string[] = [
    "checkbox",
    "coinorderid",
    // "coinordertype",
    "coindate",
    "cointime",
    "cointransactionid",
    "coinpaymentmethod",
    "coinamount",
  ];
  dataSource = new MatTableDataSource<PeriodicElement>(ELEMENT_DATA);
  dataSource1 = new MatTableDataSource<PeriodicElement1>(ELEMENT_DATA1);
  @ViewChild("MatPaginator", { static: true }) paginator: MatPaginator;
  @ViewChild("MatPaginator1", { static: true }) paginator1: MatPaginator;
  @ViewChild(MatSort, { static: true }) sort: MatSort;

  optionsFrom: DatepickerOptions = {
    minYear: 2017,
    maxDate: new Date(),
  };
  userId: any;
  moneyTransaction: any
  coinTransaction: any
  private sub: any;

  constructor(
    private route: ActivatedRoute,
    private api: AuthenticationService
  ) { }

  ngOnInit() {
    this.route.params.subscribe(params => {
      this.userId = params.id
    });
    this.getTransaction()
  }

  getTransaction() {
    var currentDate = new Date().getTime()
    this.api.getAllTransaction().subscribe((result) => {
      this.allTransaction = result['response']
      for (let x = 0; x < this.allTransaction.length; x++) {
        var finalD = currentDate - new Date(this.allTransaction[x].createdAt).getTime()
        var Difference_In_Days = parseInt(`${finalD / (1000 * 3600 * 24)}`);
        if (Difference_In_Days <= 0) { this.allTransaction[x].duration = 'Hourly' }
        if (Difference_In_Days > 0 && Difference_In_Days <= 1) { this.allTransaction[x].duration = 'Daily' }
        if (Difference_In_Days > 1 && Difference_In_Days < 7) { this.allTransaction[x].duration = 'Weekly' }
        if (Difference_In_Days >= 7) { this.allTransaction[x].duration = 'Monthly' }
      }
      var userTransaction = this.allTransaction.filter(elem => {
        if (elem.userId == this.userId) {
          return elem
        }
      })
      this.moneyTotal = 0
      this.moneyTransaction = userTransaction.filter(elem => {
        if (elem.transactionId != "") {
          this.moneyTotal += elem.price
          return elem
        }
      })
      this.dataSource = new MatTableDataSource<PeriodicElement>(this.moneyTransaction);
      this.dataSource.paginator = this.paginator;
      this.dataSource.sort = this.sort;

      this.coinTotal = 0
      this.coinTransaction = userTransaction.filter(elem => {
        if (elem.transactionId == "") {
          this.coinTotal += elem.price
          return elem
        }
      })
      console.log(this.coinTransaction)
      this.dataSource1 = new MatTableDataSource<any>(this.coinTransaction);
      // this.dataSource1.paginator1 = this.paginator1;
      this.dataSource1.sort = this.sort;
    })
  }


  sortNormal(eve: any, tableType) {
    if (eve.target.value == 0) {
      if (tableType == 'money') {
        this.moneyTransaction.sort((a, b) => (a.title > b.title) ? 1 : -1)
        this.dataSource = new MatTableDataSource<PeriodicElement>(this.moneyTransaction);
        this.dataSource.paginator = this.paginator;
        this.dataSource.sort = this.sort;
      }
      if (tableType == 'coin') {
        this.coinTransaction.sort((a, b) => (a.title > b.title) ? 1 : -1)
        this.dataSource1 = new MatTableDataSource<any>(this.coinTransaction);
        this.dataSource1.paginator = this.paginator;
        this.dataSource1.sort = this.sort;
      }
    }
    if (eve.target.value == 1) {
      if (tableType == 'money') {
        this.moneyTransaction.sort((a, b) => (a.title < b.title) ? 1 : -1)
        this.dataSource = new MatTableDataSource<PeriodicElement>(this.moneyTransaction);
        this.dataSource.paginator = this.paginator;
        this.dataSource.sort = this.sort;
      }
      if (tableType == 'coin') {
        this.coinTransaction.sort((a, b) => (a.title < b.title) ? 1 : -1)
        this.dataSource1 = new MatTableDataSource<any>(this.coinTransaction);
        this.dataSource1.paginator = this.paginator;
        this.dataSource1.sort = this.sort;
      }
    }
  }

  doFilter(val, tableType) {
    if (tableType == 'money') { this.dataSource.filter = val.trim().toLocaleLowerCase() }
    if (tableType == 'coin') { this.dataSource1.filter = val.trim().toLocaleLowerCase() }
  }

  fromDate(dateValue) {
    this.fromdate = new Date(dateValue).getTime()
  }
  selectedItems
  sentData
  toDate(dateValue) {
    this.todate = new Date(dateValue).getTime()
    this.moneyTransaction.forEach((item, index) => {
      if (item.createdAt >= this.fromdate && item.createdAt <= this.todate) {
        this.selectedItems.push(item);
      }
    });
    this.dataSource = new MatTableDataSource<PeriodicElement>(this.selectedItems);
    this.dataSource.paginator = this.paginator;
    this.dataSource.sort = this.sort;

    this.coinTransaction.forEach((item, index) => {
      if (item.createdAt >= this.fromdate && item.createdAt <= this.todate) {
        this.sentData.push(item);
      }
    });
    this.dataSource1 = new MatTableDataSource<any>(this.sentData);
    this.dataSource1.paginator = this.paginator;
    this.dataSource1.sort = this.sort;

  }

  filterByDate(eve: any, tableType) {
    if (eve.target.value == "a") {
      this.getTransaction()
    }
    if (eve.target.value == 'd') {
      if (tableType == 'money') {
        var filterData = this.moneyTransaction.filter((elem) => {
          if (elem.duration == 'Daily') {
            return elem
          }
        })
      }
      if (tableType == 'coin') {
        var filterSentData = this.coinTransaction.filter((elem) => {
          if (elem.duration == 'Daily') {
            return elem
          }
        })
      }
    }
    if (eve.target.value == 'w') {
      if (tableType == 'money') {
        var filterData = this.moneyTransaction.filter((elem) => {
          if (elem.duration == 'Weekly') {
            return elem
          }
        })
      }
      if (tableType == 'coin') {
        var filterSentData = this.coinTransaction.filter((elem) => {
          if (elem.duration == 'Weekly') {
            return elem
          }
        })
      }
    }
    if (eve.target.value == 'm') {
      if (tableType == 'money') {
        var filterData = this.moneyTransaction.filter((elem) => {
          if (elem.duration == 'Monthly') {
            return elem
          }
        })
      }
      if (tableType == 'coin') {
        var filterSentData = this.coinTransaction.filter((elem) => {
          if (elem.duration == 'Monthly') {
            return elem
          }
        })
      }
    }
    this.dataSource = new MatTableDataSource<PeriodicElement>(filterData);
    this.dataSource.paginator = this.paginator;
    this.dataSource.sort = this.sort;

    this.dataSource1 = new MatTableDataSource<any>(filterSentData);
    this.dataSource1.paginator = this.paginator;
    this.dataSource1.sort = this.sort;
  }


  filterByCoin(eve, tableType) {
    var coinNumber = eve.target.value
    if (tableType == 'money') {
      if (coinNumber == 1) {
        var moneyFilter = this.moneyTransaction.filter((elem) => {
          if (elem.coinType == 1) {
            return elem
          }
        })
      }
      if (coinNumber == 2) {
        var moneyFilter = this.moneyTransaction.filter((elem) => {
          if (elem.coinType == 2) {
            return elem
          }
        })
      }
      this.dataSource = new MatTableDataSource<PeriodicElement>(moneyFilter);
      this.dataSource.paginator = this.paginator;
      this.dataSource.sort = this.sort;
    }

    if (tableType == 'coin') {
      if (coinNumber == 1) {
        var moneyFilter = this.moneyTransaction.filter((elem) => {
          if (elem.coinType == 1) {
            return elem
          }
        })
      }
      if (coinNumber == 2) {
        var moneyFilter = this.moneyTransaction.filter((elem) => {
          if (elem.coinType == 2) {
            return elem
          }
        })
      }
      this.dataSource1 = new MatTableDataSource<any>(moneyFilter);
      this.dataSource1.paginator = this.paginator1;
      this.dataSource1.sort = this.sort;
    }

  }


}
