import { Component, OnInit, ViewChild } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from "@angular/forms";
import { MatPaginator, MatSort, MatTableDataSource } from "@angular/material";
import { AuthenticationService } from "../../service/authentication.service";
import { ToastrService } from "ngx-toastr"
declare var $: any;

export interface PeriodicElement {
  srno: string;
}

const ELEMENT_DATA: PeriodicElement[] = [
  { srno: "1", },
  { srno: "2" },
  { srno: "3" },
];




@Component({
  selector: 'app-banner-management',
  templateUrl: './banner-management.component.html',
  styleUrls: ['./banner-management.component.css']
})
export class BannerManagementComponent implements OnInit {
  addBannerForm: FormGroup

  displayedColumns: string[] = [
    "srno",
    "image",
    "action",
  ];

  dataSource = new MatTableDataSource<PeriodicElement>();
  single: any[];
  picture: false;
  banner_id : string;
  curr_banner_image : string;

  @ViewChild("MatPaginator", { static: true }) paginator: MatPaginator;

  constructor(
    private formBuilder: FormBuilder,
    private service: AuthenticationService,
    private toaster: ToastrService,
    private fb: FormBuilder
  ) { }

  ngOnInit() {
    this.getAllBanner()
    this.addBannerForm= this.fb.group({
      myBanner: ["", Validators.required]
    })
  }

  myBanner = []
  getAllBanner() {
    this.service.getBanner().subscribe(res => {
      this.myBanner = res['data']
      console.log('banner image', this.myBanner)
      this.dataSource = new MatTableDataSource(this.myBanner)
      this.dataSource.paginator = this.paginator
    })
  }

  addBanner() {
    debugger

    let data = new FormData();
    if (this.picture != false) {
      data.append("bannerImage", this.picture);
    }
    data.append("tempImage", JSON.stringify(this.myBanner));
    this.service.addBanner(data).subscribe(res => {
      console.log(res)
      $('#closeAddBanner').click()
      this.picture = false
      document.getElementById("imagefile1").setAttribute('src', 'assets/img/Upload-icon.png');
      this.getAllBanner()
    })
  }

  removeImage(index) {
    this.myBanner.splice(index, 1);
    console.log(this.myBanner)
  }

  singleFiles1(event) {
    const file1 = event.target.files[0]

    this.single = [];
    var singleFiles = event.target.files;
    if (singleFiles) {
      for (var file of singleFiles) {
        var singleReader = new FileReader();
        singleReader.onload = (e: any) => {
          this.single.push(e.target.result);
          document.getElementById("imagefile1").setAttribute('src', e.target.result);
        }
        singleReader.readAsDataURL(file);
      }
    }
    this.picture = event.target.files[0];
    console.log(this.picture, "::::::::::::::::::::::::::;singleFiles1 brooooooo");
  }

  blockid(id) {
    this.banner_id = id;
  }

 
  blockunblockbanner() {
    this.service.blockUnblockBanner({ _id: this.banner_id }).subscribe(
      (res) => {
        this.getAllBanner();
        console.log(res, "After api hit");
        if(res["messsage"] = "Unblocked successfully"){
          setTimeout(() => {
            $("#unsuccess-block").modal("hide");
          }, 900);
        }
        if(res["message"] = "Blocked successfully"){
          setTimeout(() => {
            $("#success-block").modal("hide");
          }, 900);
        }
      },
      (error) => {
        console.log(error);
      }
    );
  }

  deleteBanner() {
    var datadelete = {
      _id: this.banner_id
    };

    this.service.deleteBanner(datadelete).subscribe(
      (d) => {
        // console.log(this.deletesub,'data')
        console.log(d, "data here is:::::::::::::::::::::::::::::::: ");
        if (d) {
          this.getAllBanner();
        }
        setTimeout(() => {
          $("#deleted-success").modal("hide");
        }, 1200);
      },
      (error) => {
        console.log(error);
      }
    );
  }

  setbannerimage(img) {
    this.curr_banner_image = img
  }
  
}
