  saveMemberShip(submitType) {
    debugger
    var checkedOfferRupee = this.membershipPackageRupee.filter((elem) => {
      if (elem.checkBox == true) {
        return elem
      }
    })
    var realMoneyArray = []
    for (let x = 0; x < checkedOfferRupee.length; x++) {
      realMoneyArray.push(checkedOfferRupee[x].name)
    }
    var checkedOfferGold = this.membershipPackageGold.filter((elem) => {
      if (elem.checkBox == true) {
        return elem
      }
    })
    var goldMoneyArray = []
    for (let x = 0; x < checkedOfferGold.length; x++) {
      goldMoneyArray.push(checkedOfferGold[x].name)
    }
    var checkedOfferSilver = this.membershipPackageSilver.filter((elem) => {
      if (elem.checkBox == true) {
        return elem
      }
    })
    var silverMoneyArray = []
    for (let x = 0; x < checkedOfferSilver.length; x++) {
      silverMoneyArray.push(checkedOfferSilver[x].name)
    }
    // if (checkedOfferRupee.length < 1) {
    //   this.toaster.error("Please select atleast one Silver Membership package", "Error")
    //   return;
    // }
    // if (submitType == 1) {
    //   if (checkedOfferGold.length < 1) {
    //     this.toaster.error("Please select atleast one Gold Membership package", "Error")
    //     return;
    //   }
    // }
    if (this.dateFrom == undefined || this.dateTo == undefined) {
      this.toaster.error("Please select both date", "Error")
      return;
    }
    console.log(this.fromdate)
    if (submitType == 1) {
      this.submitted = true;
      // if (this.forMembership.invalid) { return }
      let packages = this.selectedItems1.map((item: any) => { return item._id });
      var dataToSaveRupee = {
        title: this.forMembership.value.title,
        durationFrom: this.dateFrom,
        durationTo: this.dateTo,
        paymentType: this.forMembership.value.paymentType,
        realMoney: realMoneyArray,
        goldMoneyArray: goldMoneyArray,
        silverMoneyArray: silverMoneyArray,
        discount: this.forMembership.value.discount,
        price: this.forMembership.value.price,
        package: packages,
        offerType: submitType
      }

      console.log(dataToSaveRupee);

      this.api.addOffer(dataToSaveRupee).subscribe((result) => {
        this.toaster.success("Offer added Successfully", "Success")
        this.router.navigateByUrl('/management/promotions')
      })
    }
    if (submitType == 2) {
      this.submitted = true;
      if (this.goldMembership.invalid) { return; }
      var dataToSaveGold = {
        title: this.goldMembership.value.goldTitle,
        durationFrom: this.dateFrom,
        durationTo: this.dateTo,
        realMoney: realMoneyArray,
        goldMoneyArray: goldMoneyArray,
        silverMoneyArray: silverMoneyArray,
        discount: this.goldMembership.value.discount,
        price: this.goldMembership.value.priceGold,
        offerType: submitType
      }
      this.api.addOffer(dataToSaveGold).subscribe((result) => {
        this.toaster.success("Offer added Successfully", "Success")
        this.router.navigateByUrl('/management/promotions')
      })
    }

    if (submitType == 3) {
      this.submitted = true;
      console.log(this.inAppMembership.value)
      if (this.inAppMembership.invalid) { return; }
      var dataToSaveSilver = {
        title: this.inAppMembership.value.inAppTitle,
        durationFrom: this.dateFrom,
        durationTo: this.dateTo,
        realMoney: realMoneyArray,
        goldMoneyArray: goldMoneyArray,
        silverMoneyArray: silverMoneyArray,
        purchaseItemByGold: this.inAppMembership.value.purchaseByGold,
        discount: this.inAppMembership.value.discount,
        goldCoin: this.inAppMembership.value.priceSilver,
        offerType: submitType
      }
      this.api.addOffer(dataToSaveSilver).subscribe((result) => {
        this.toaster.success("Offer added Successfully", "Success")
        this.router.navigateByUrl('/management/promotions')
      })
    }

    console.log('s', this.forMembership)
  }


