  fileChange;
  singleFiles1(event) {
    this.fileChange = true;
    console.log("*********************");
    const file1 = event.target.files[0];

    this.single = [];
    var singleFiles = event.target.files;
    if (singleFiles) {
      for (var file of singleFiles) {
        var singleReader = new FileReader();
        singleReader.onload = (e: any) => {
          this.single.push(e.target.result);
          document
            .getElementById("imagefile1")
            .setAttribute("src", e.target.result);
            this.myEditImage = e.target.result
        };
        singleReader.readAsDataURL(file);
      }
    }
    this.picture = event.target.files[0];
    console.log(this.picture, "::::::::::::::::::::::::::;image");
  }

  updateBadgeSetting(){
    this.service.myAppSetting({
      settingFor:'badge',
      wearBadge:this.wearBadge,
      badgeType:this.badgeType
    }).subscribe( result => {
      console.log( result )
      this.service.toaster("Success", "Badge setting Updated");
    } )
  }
  badgeSetting
