  //  fetching the data into for for edit
  editId;
  myEditImage = "assets/img/Upload-icon.png"
  getEditValue(element) {
    if( element.badgeType == 'achivement' ){
      this.myCoinType = true
      this.myReachedCoin = true
      this.noOfWeek = false
      this.noOfUser = false
      this.myBadgeNameShow = true
      this.myBadgeShow = true
      this.myBadgeImage = true
    }
    if( element.badgeType == 'honour' ){
      this.myCoinType = false
      this.myReachedCoin = false
      this.noOfWeek = true
      this.noOfUser = true
      this.myBadgeNameShow = true
      this.myBadgeShow = false
      this.myBadgeImage = true
    }
    if( element.badgeName == 'Crystal collector' ){
      this.coinTypeGold = false
      this.coinTypeSilver = true
    }
    else{
      this.coinTypeGold = true
      this.coinTypeSilver = false
    }
    if(element.badgeName == 'Slick' ){
      this.myReachedCoin = false
    }else{
      this.myReachedCoin = true
    }
    if( element.badgeName == 'Sign-in' ){
      this.noOfDay = true
    }else{
      this.noOfDay = false
    }
    if( element.badgeName == "Loyalist"){
      console.log('ddddd')
      this.myCoinType = false
      this.myReachedLevel = true
      this.forSlickReached = false
    }else{
      this.myReachedLevel = false
      this.myCoinType = true
      this.forSlickReached = true
    }
    if( element.badgeName == 'Slick' ){
      this.forSlickReached = false
    }else{
      this.forSlickReached = true
    }

    this.editId = element._id;
    ( element.badgeImage == null || element.badgeImage == undefined || element.badgeImage == "" ) 
    ? this.myEditImage = "assets/img/Upload-icon.png"
    : this.myEditImage = element.badgeImage
    
    this.editAchievement.patchValue({
      badgeType: element.badgeType,
      badgeName: element.badgeName,
      coinType: element.coinType,
      reachedCoin: element.reachedCoin,
      badgeNumber:element.badgeNumber,
      badge: element.badge,
      noOfWeek: element.noOfWeek,
      noOfUser: element.noOfUser,
      myReachedLevel:element.myReachedLevel    
    });
  }



isActive = true
