  submitTask(num){
    console.log( 'num' , parseInt(num) )
      if( num == 1 ){
        console.log( this.forDaily )

        var checkedTask = this.forDaily.filter( (elem) =>{
          if( elem.taskCheckbox == true ){
            return elem
          }
        } )
        this.api.addTask({data:checkedTask,taskType:num}).subscribe( ( result ) => {
          console.log('result' , result)
          this.getTask()
          this.toaster.success(result['message'],"Success")
        })
      }

      if( num == 2 ){
        var checkedTask = this.forImprovement.filter( (elem) =>{
          if( elem.taskCheckbox == true ){
            return elem
          }
        } )
        this.api.addTask({data:checkedTask,taskType:num}).subscribe( ( result ) => {
          console.log('result' , result)
          this.getTask()
          this.toaster.success(result['message'],"Success")
        })
      }

      if( num == 3 ){
        console.log( this.forDay )
        this.api.addTask({data:this.forDay,taskType:num}).subscribe( ( result ) => {
          console.log('result' , result)
          this.getTask()
          this.toaster.success(result['message'],"Success")
        })
      }
  }

  getTask(){
    this.api.getTask({taskType:1}).subscribe( ( result ) => {
      this.forDailyTable = result['data']
      this.dataSource = new MatTableDataSource<PeriodicElement>(this.forDailyTable);
      this.dataSource.paginator = this.paginator;
      this.dataSource.sort = this.sort;
    } )

    this.api.getTask({taskType:2}).subscribe( ( result ) => {
      this.forImprovementTable = result['data']
      this.dataSource1 = new MatTableDataSource<any>(this.forImprovementTable);
      this.dataSource1.paginator = this.paginator1;
      this.dataSource1.sort = this.sort;
    } )

    this.api.getTask({taskType:3}).subscribe( ( result ) => {
      this.forDayTable = result['data']
      this.dataSource2 = new MatTableDataSource<any>(this.forDayTable);
      this.dataSource2.paginator = this.paginator2;
      this.dataSource2.sort = this.sort;
    } )
  }


  blockUnblockText
  blockId
  blockUnblock( elem:any ){
    this.blockId = elem._id
      if( elem.isBlocked == 0 ){
        this.blockUnblockText = 'Block'
      }
      if( elem.isBlocked == 1 ){
        this.blockUnblockText = 'Unblock'
      }
  }

  blockunblockuser(){
    this.api.blockTask({taskId:this.blockId}).subscribe( ( result ) => {
      this.getTask()
    } )
  }

  taskId
  editTask( elem:any ){
    this.taskId = elem._id
    this.editForm.patchValue({
      taskName:elem.taskName,
      rewardPoint:elem.rewardPoint
    })
  }

  dailytaskedit(){
    this.submitted = true;
    if (this.editForm.invalid) return;

    this.api.editTask({
      taskId:this.taskId,
      taskName:this.editForm.value.taskName,
      rewardPoint:this.editForm.value.rewardPoint
    }).subscribe( ( result ) => {
      this.toaster.success( "Updated successfully","Success" )
      this.getTask()
      $('#closeModal').click()
    } )
  }


